import EventEmitter from "./EventEmittter";

const STATUS = {
  CONNECTING : 'Connecting',
  CONNECTED : 'Connected',
  DISCONNECT : 'Disconnected'
};

export default class ConfigLoader extends EventEmitter {
  websocketClient;
  constructor() {
    super();
    this.connect();
  }

  connect() {
    try {
      this.DEFAULT_RETRY_CONNECT_TIME = 5000;
      this.websocketClient =  new WebSocket('ws://localhost:15883/changeConfig');
      this.retryConnectTime = this.DEFAULT_RETRY_CONNECT_TIME;
      this.currentStatus = STATUS.DISCONNECT;
    } catch (e) {
      console.log(e);

      return;
    }

    this.websocketClient.onopen = () => {
      this.currentStatus = STATUS.CONNECTED;
      this.emit('connect', this.currentStatus);
    };

    this.websocketClient.onmessage = (result) => {

      var config = JSON.parse(result.data);
      this.emit('change', config);

    };

    this.websocketClient.onclose = (event) => {
      console.log('onclose');
      this.currentStatus = STATUS.DISCONNECT;
      this.emit('connect', this.currentStatus);

      setTimeout(() => {
        this.connect();
      }, this.retryConnectTime);
    };

    this.currentStatus = STATUS.CONNECTING;
    this.emit('connect', this.currentStatus);
  }

  send(message) {
    if (message === undefined) {
      return;
    }

    if (this.websocketClient === undefined) {
      return;
    }

    try {
      this.websocketClient.send(message);
    } catch (e) {
    }
  }
}


