import React, { Component } from 'react';
import './App.css';
import {Button, Label, Grid} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';
import MainConfig from "./components/MainConfig";
import Files from 'react-files'
import FileSaver from 'file-saver'
import ConfigLoader from "./utils/ConfigLoader";

class App extends Component {
    state = {
      configLoaderStatus: 'Started',
        config : {
            globalSetting : {
                windowSize : 10,
              volume : 50
            },
            settings : [
                {
                    isEnable: true,
                    frequencyRange: [50, 100],
                    thresholdMultiplier: 2.15,
                    volume: 1,
                    targets: [{
                        position: ['VestFront', 'VestBack', 'Left'],
                        points: [{x : 0, y : 0}, {x : 0.1, y: 0.2}]
                    }
                    ]
                },{
                    isEnable: true,
                    frequencyRange: [50, 100],
                    thresholdMultiplier: 2.15,
                    volume: 1,
                    targets: [{
                        position: ['VestFront'],
                        points: [{x : 0, y : 0}, {x : 0.1, y: 0.2}]
                    }
                    ]
                }
            ]
        }
    };

    configLoader;

    constructor(props) {
      super(props);
      this.configLoader = new ConfigLoader();
      this.configLoader.on('change', (config) => {
        this.setState({config: config});
      });
      this.configLoader.on('connect', (status) => {
        this.setState({configLoaderStatus: status});
      });

    }

  render() {
    return (
      <div className="App">
          <Files as="text"
                 accepts={['.json']}
                 className='files-dropzone'
                 onChange={(files) => {
                     files.forEach(file => {
                         let fileReader = new FileReader();
                         fileReader.onload = (event) => {
                             let config = JSON.parse(event.target.result)

                             if (!config.globalSetting) {
                                 return;
                             }

                             if (!config.settings) {
                                 return;
                             }

                             this.setState({config : config});
                         };
                      fileReader.readAsText(file);
              });
          }}>
            <Button>Import</Button>
          </Files>
          <Button onClick={() => {
              var blob = new Blob([JSON.stringify(this.state.config, null, 2)], {type: "application/json;charset=utf-8"});
              FileSaver.saveAs(blob, 'Config.json');
          }}>Export</Button>

        <div><Label>{this.state.configLoaderStatus}</Label></div>
          <MainConfig config={this.state.config} saveConfig={(config) => {
              this.setState({config : config});
              this.configLoader.send(JSON.stringify(config));
          }}></MainConfig>
      </div>
    );
  }
}

export default App;



