import React from 'react';
import { Container, Header, Grid, Table, Button, Input } from 'semantic-ui-react'
import Setting from './Setting';

const copyObject = (obj) => {
    let cpy = JSON.parse(JSON.stringify(obj));
    return cpy;
};

const newSetting = () => {
    let setting =
    {
        isEnable: true,
        frequencyRange: [100, 200],
        thresholdMultiplier: 2.15,
        volume: 1,
        targets: [{
            position: ['VestFront', 'VestBack'],
            points: []
        }
        ]
    };

    return setting;
};

const MainConfig = ({config, saveConfig}) =>
        <Container>
            <GlobalConfig config={config.globalSetting} onChange={(changed) => {
                let editableConfig = copyObject(config);
                editableConfig.globalSetting = changed;
                saveConfig(editableConfig);
            }}></GlobalConfig>

            <Header as='h2'>Individual Config</Header>
            <Table celled>
                <Table.Header>
                    <Table.Row>
                        <Table.HeaderCell  width={1}>Delete</Table.HeaderCell>
                        <Table.HeaderCell  width={1}>Enabled</Table.HeaderCell>
                        <Table.HeaderCell  width={2}>Frequency Range</Table.HeaderCell>
                        <Table.HeaderCell  width={1}>threshold
                            Multiplier</Table.HeaderCell>
                        <Table.HeaderCell  width={1}>volume</Table.HeaderCell>
                        <Table.HeaderCell >Target</Table.HeaderCell>
                        <Table.HeaderCell  width={1}>
                            <Button onClick={() => {
                                let editableConfig = copyObject(config);
                                editableConfig.settings.push(newSetting());
                                saveConfig(editableConfig);
                            }}>Add</Button>
                        </Table.HeaderCell>
                    </Table.Row>
                </Table.Header>

                <Table.Body>
                    {config.settings.map((setting, idx) =>
                                <Setting key={idx} setting={setting} saveSetting = {(changed) => {
                                    let editableConfig = copyObject(config);
                                    editableConfig.settings[idx] = changed;
                                    saveConfig(editableConfig);
                                }} deleteSetting={() => {
                                    let editableConfig = copyObject(config);
                                    editableConfig.settings.splice(idx, 1);
                                    saveConfig(editableConfig);
                                }}></Setting>
                    )}
                </Table.Body>
            </Table>
        </Container>;


export default MainConfig;

const GlobalConfig = ({config, onChange}) =>
    <div>
        <Header as='h2'>Global Config</Header>
        <Grid columns={3} divided>
            <Grid.Row>
                <Grid.Column>
                    <label>Window Size</label>
                </Grid.Column>
                <Grid.Column>
                    <Input value={config.windowSize} onChange={(e, {value}) => {
                        let newVal = Math.max(1, parseInt(value, 10));
                        let cpy = copyObject(config);
                        cpy.windowSize = newVal;
                        onChange(cpy);
                    }}/>
                </Grid.Column>
            </Grid.Row>
            <Grid.Row>
                <Grid.Column>
                    <label>Global Volume</label>
                </Grid.Column>
                <Grid.Column>
                    <Input value={config.volume} onChange={(e, {value}) => {
                      let newVal = Math.max(1, parseInt(value, 10));
                      let cpy = copyObject(config);
                      cpy.volume = newVal;
                      onChange(cpy);
                    }}/>
                </Grid.Column>
            </Grid.Row>
        </Grid>
    </div>






