const config = {
    globalSetting : {
        windowSize : 10
    },
    settings : [
        {
            isEnable: true,
            frequencyRange: [50, 100],
            targets: [{
                position: ['VestFront', 'VestBack', 'Left'],
                points: [{x : 0, y : 0}, {x : 0.1, y: 0.2}]
            }
            ]
        },{
            isEnable: true,
            frequencyRange: [50, 100],
            targets: [{
                position: ['VestFront'],
                points: [{x : 0, y : 0}, {x : 0.1, y: 0.2}]
            }
            ]
        }
    ]
};

const settings = (state = config.settings, action) => {
    switch (action.type) {
        case 'ADD_SETTING':
            return state;
        case 'SAVE_SETTING':
            return state;
        case 'DELETE_SETTING':
            return state;
        default:
            return state;
    }
}

export default settings;


