import React from 'react';
import {Table, Icon, Label, Checkbox, Dropdown, Popup, Button, Input } from 'semantic-ui-react'

const options = [
    { key: 'VestFront', text: 'VF', value: 'VestFront' },
    { key: 'VestBack', text: 'VB', value: 'VestBack' },
    { key: 'Left', text: 'Left', value: 'Left' },
    { key: 'Right', text: 'Right', value: 'Right' }
];

const copyObject = (obj) => {
    let cpy = JSON.parse(JSON.stringify(obj));
    return cpy;
};

const copySettingFromProps = (props) => {
    return copyObject(props.setting);
};

class Setting extends React.Component {
    handleToggleEnable() {
        let editableSetting = copySettingFromProps(this.props);
        editableSetting.isEnable = !editableSetting.isEnable;

        this.saveChange(editableSetting);
    }

    deletePoint(targetIndex, pointIndex) {
        let editableSetting = copySettingFromProps(this.props);
        editableSetting.targets[targetIndex].points.splice(pointIndex, 1);
        this.saveChange(editableSetting);
    }

    addPoint(targetIndex, point) {
        let editableSetting = copySettingFromProps(this.props);
        editableSetting.targets[targetIndex].points.push(point);
        this.saveChange(editableSetting);
    }

    handlePosition(targetIndex, positions) {
        let editableSetting = copySettingFromProps(this.props);
        editableSetting.targets[targetIndex].position = positions;
        this.saveChange(editableSetting);
    }

    saveChange(newSetting) {
        if (this.props.saveSetting) {
            this.props.saveSetting(newSetting)
        } else {
            console.log('saveChange not defined', newSetting);
        }
    }

    render() {
        var setting = this.props.setting;
        return (
            <Table.Row>
                <Table.Cell>
                    <Icon name='delete' style={{width: '10px'}} onClick={() => {
                        this.props.deleteSetting();
                    }}></Icon>
                </Table.Cell>
                <Table.Cell>
                    <Checkbox toggle checked={setting.isEnable} onChange={() => this.handleToggleEnable()}/>

                </Table.Cell>
                <Table.Cell>
                    <FrequencyRange range={setting.frequencyRange} onChange={(event, {value}) => {
                        let editableSetting = copySettingFromProps(this.props);
                        editableSetting.frequencyRange= value;
                        this.saveChange(editableSetting);
                    }}/>
                </Table.Cell>
                <Table.Cell>
                    <Input style={{width: '80px'}}
                           onChange={(e, {value}) => {
                               let thr = parseFloat(value);
                               let editableSetting = copySettingFromProps(this.props);
                               editableSetting.thresholdMultiplier= thr;
                               this.saveChange(editableSetting);
                           }}
                           value={setting.thresholdMultiplier}></Input>
                </Table.Cell>
                <Table.Cell>
                    <Input style={{width: '80px'}}
                           value={setting.volume}
                           onChange={(e, {value}) => {
                            let volume = parseFloat(value);
                            let editableSetting = copySettingFromProps(this.props);
                            editableSetting.volume= volume;
                            this.saveChange(editableSetting);
                    }}></Input>
                </Table.Cell>
                <Table.Cell colSpan='2'>
                    <div>
                        <Table celled>
                            <Table.Body>
                                {
                                    setting.targets.map((target, targetIndex) =>
                                        <Table.Row key={targetIndex}>
                                            <Table.Cell width={4}>
                                                <Dropdown placeholder='Position' fluid multiple selection
                                                          options={options} value={target.position}
                                                          onChange={(event, {value}) => {
                                                                this.handlePosition(targetIndex, value);
                                                            }}/>
                                            </Table.Cell>
                                            <Table.Cell>
                                                <Target target={target}
                                                        addPoint={(point) => {
                                                            this.addPoint(targetIndex, point);
                                                        }}
                                                        deletePoint={(pointIndex) => {
                                                            this.deletePoint(targetIndex, pointIndex);
                                                        }}>
                                                </Target>
                                            </Table.Cell>

                                        </Table.Row>
                                    )
                                }
                            </Table.Body>
                        </Table>

                    </div>
                </Table.Cell>
            </Table.Row>)
    }
}

export default Setting;

class Target extends React.Component {
    state = {isOpen: false, point : {x: 0.5, y: 0.5}};
    handleOpen = () => {
        this.setState({ isOpen: true });
    };
    handleClose = () => {
        this.setState({isOpen: false})
    };

    render() {
        let target = this.props.target;
        let addPoint = this.props.addPoint;
        let deletePoint = this.props.deletePoint;
        return (
            <div>
            {target.points.map((point, idx) =>
                <Point key={idx} point={point} deletePoint={() => {deletePoint(idx)}}/>
            )}
                <Popup wide trigger={<Button>
                    <Icon name='add'></Icon>
                </Button>} on='click'
                   onOpen={this.handleOpen}
                   onClose={this.handleClose}
                   open={this.state.isOpen}>
                            <Input label='x' value={this.state.point.x}
                                   onChange={(e, {value}) => {
                                   var x = Math.min(1, Math.max(0, parseFloat(value)));
                                    this.setState({point: {x : x, y : this.state.point.y}});
                                   }}>
                            </Input>
                            <Input label='y' value={this.state.point.y}
                                onChange={(e,  {value}) => {
                                    var y = Math.min(1, Math.max(0, parseFloat(value)));
                                    this.setState({point : {x: this.state.point.x, y: y}});
                                }}>
                            </Input>
                            <Button content={'Add'} onClick={e => {
                                e.preventDefault();
                                addPoint(this.state.point);
                            }}></Button>

            </Popup>
        </div>);
    }
}

const Point = ({point, deletePoint}) =>
    <Label>
        x: {point.x}, y: {point.y}
        <Icon name='delete' onClick={() => deletePoint()}/>
    </Label>;

const FrequencyRange = ({range, onChange}) =>
    <div>
        <Input onChange={(e, {value}) => {
            let change = Math.min(parseInt(value, 10), range[1]);
            let arr = [change, range[1]];
            onChange(e, {value : arr});
        }} value={range[0]} style={{width : '70px'}}>
        </Input>
        <span>hz ~ </span>
        <Input onChange={(e, {value}) => {
            let change = Math.max(parseInt(value, 10), range[0]);
            let arr = [range[0], change];
            onChange(e, {value : arr});
        }} value={range[1]}
               style={{width : '70px'}}>
        </Input>
        <span>hz </span>
    </div>;


